import React, {Component} from 'react';
import './StylesNoticias.css';
import { IoIosPaper } from "react-icons/io";
import axios from  'axios';


export default class Noticias extends Component{

constructor(props) {
    super(props);
        this.state = {
            artigo1: {},
            artigo2: {},
            artigo3: {},
            artigo4: {},
        };
    
    }


async componentDidMount() {

    try{
        const noticia = await axios.get('http://newsapi.org/v2/top-headlines?country=br&category=business&apiKey=75335a67772745d6afa82c75d343b8d6');
    

        this.setState(this.state.artigo1 = noticia.data.articles[0]);
        this.setState(this.state.artigo2 = noticia.data.articles[1])
        this.setState(this.state.artigo3 = noticia.data.articles[2])
        this.setState(this.state.artigo4 = noticia.data.articles[3])

        console.log(this.state.artigo1)
        console.log(this.state.artigo2)

        console.log(this.state.artigo3)

        console.log(this.state.artigo4)

    }catch(err) {
        console.log(err)
    }


    // console.log(noticia.data.articles[0])
    // console.log(noticia.data.articles[1])
    // console.log(noticia.data.articles[2])
    // console.log(noticia.data.articles[4])

}
    
    render(){
        return(
            <div id="containerNoticias">
                <div className="containerTituloNoticias">
                    <IoIosPaper className="iconNoticias" />
                    <h1 className="NotTitulo" > Últimas Notícias </h1>
                </div>

                <div className="itensNoticias">
                    <div href="" className="itemNoticia" id="itemNoticias1">
                        <img src={this.state.artigo1.urlToImage} id="imagemNot1" className="imagemNoticias"></img>
                        <p className="descricaoNoticias" maxlength="10" >{this.state.artigo1.title}</p>
                        <hr className="linha" />

                        <p className="dataNoticias"></p>
                    </div>

                    <div className="itemNoticia" id="itemNoticias2">
                        <img src={this.state.artigo2.urlToImage} id="imagemNot2" className="imagemNoticias"></img>
                        <p className="descricaoNoticias">{this.state.artigo2.title}</p>
                        <hr className="linha" />
                        <p className="dataNoticias"></p>
                    </div>

                    <div className="itemNoticia" id="itemNoticias3">
                        <img  src={this.state.artigo3.urlToImage} id="imagemNot3" className="imagemNoticias"></img>
                        <p className="descricaoNoticias">{this.state.artigo3.title}</p>
                        <hr className="linha" />
                        <p className="dataNoticias"></p>
                    </div>

                    <div className="itemNoticia" id="itemNoticias4">
                        <img src={this.state.artigo4.urlToImage} id="imagemNot4" className="imagemNoticias"></img>
                        <p className="descricaoNoticias">{this.state.artigo4.title}</p>
                        <hr className="linha" />
                        <p className="dataNoticias"></p>
                    </div>

                </div>
                <a  href="#" className="leiaMais"> LEIA MAIS</a>
            </div>
        )
    }
}