import React, { Component } from 'react';
import './StylesServico.css';
import { FaMoneyBillAlt, FaRegSun, FaHandsHelping } from 'react-icons/fa';
import { AiOutlineHome, AiFillShop } from 'react-icons/ai';
import { RiHandHeartFill, RiUserStarFill } from 'react-icons/ri';
import { GiSchoolBag, GiDiploma } from "react-icons/gi";



export default class Servicos extends Component{
    render() {
        return (
            <div id="Servicos">
                <div className="tituloServicos">
                    <FaRegSun className="iconTitulo"/>
                    <h1>Serviços</h1>
                </div>

                <div className="servicos">

                    <ul className="superiorServicos">
                        <li>
                            <button className="itemServico"> 
                                <GiDiploma className="iconsItem"/>
                                <p className="itemName">Certificado digital</p>
                            </button>
                        </li>
                        <li>
                            <button className="itemServico">
                                <GiSchoolBag className="iconsItem"/>
                                <p className="itemName">Cursos</p>
                            </button>
                        </li>
                        <li>
                            <button className="itemServico">
                                <FaMoneyBillAlt  className="iconsItem"/> 
                                <p className="itemName">Serasa</p>
                            </button>
                        </li>

                        <li>
                            <button href="#" className="itemServico">
                                <AiOutlineHome className="iconsItem" />
                                <p className="itemName">Ponto de atendimento</p>
                            </button>
                        </li>
                    </ul>

                    <ul className="inferiorServicos">
                        <li>
                            <button className="itemServico">
                                <AiFillShop className="iconsItem" />
                                <p className="itemName">Locação de salas</p>
                                </button>
                        </li>
                        <li>    
                            <button className="itemServico">
                                <RiHandHeartFill className="iconsItem" />
                                <p className="itemName">UNIMED</p>
                            </button>
                        </li>
                        <li>
                            <button className="itemServico">
                                <RiUserStarFill className="iconsItem"/>
                                <p className="itemName">Banco de talentos</p>
                            </button>
                        </li>
                        <li>
                            <button id="goCalendario" className="itemServico">
                                < FaHandsHelping className="iconsItem" />
                                <p className="itemName"> Consultoria Jurídica</p>
                            </button>
                        </li>
                    </ul>

                </div>
                
            </div>
        )
    }
}